.wrap{
  padding: 20px 16px 9px 16px;
  font-family: Arial, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "WenQuanYi Micro Hei", sans-serif;
}
.h1{
  font-size: 22px;
  line-height: 1.4;
  margin-bottom: 14px;
}
.source{
  margin-bottom: 22px;
  line-height: 20px;
  font-size: 15px;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}
.time{
  color: rgba(0,0,0,0.3);
}
.sourcename{
  margin-right: 10px;
  color: #576b95;
}
.desc{
  text-indent: 2em;
  font-size: 18px;
  color: #333;
  font-size: 17px;
  word-wrap: break-word;
  text-align: justify;
  hyphens: auto;
  margin-bottom: 19px;
}